import _tool from '@/util/tools.js';
export default {
	data() {
		return {
			DataList: [],
		}
	},
	onLoad() {
		//this.GetBookList();
	},
	onShow() {
		this.GetBookList();
	},
	// 小程序分享
	onShareAppMessage() {
	  return {
	    title: '来自毛衣小说',
	    path: 'pages/bookshelf/index'
	  }
	},
	methods: {
		GetBookList() {
			_tool.GetBookListData().then((res) => {
				this.DataList = res ? res : []
			});
		},
		editClick() {
			uni.navigateTo({
				url: '/pages/bookshelf/edit'
			})
		},
		readClick(bookData) {
			try {
			    uni.setStorageSync('BookReadData', bookData);
				uni.navigateTo({
					url: '/pages/bookshelf/read'
				})
			} catch (e) {
			    uni.showToast({
			        title: '数据异常',
			        icon: 'none'
			    });
			}
		}
	}
}
